import Reflux from 'reflux';
import actions from './actions.js';

// 
let myStore = Reflux.createStore({

    // 
    listenables: actions,
    // 
    init() {
        this.num = {count:0}
    },

    // 
    onIncrement() {
        this.num.count++;
        // 
        this.trigger(this.num.count);
    }
});

export default myStore;


