import React from 'react';
import {Button,Checkbox,Select,Radio,Switch,Form,Row,Col,Icon,Modal,Input,InputNumber,Cascader,Tooltip } from 'antd';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;

class AformInModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            visible: false,           
        }        
    }
    // 
    handleSubmit = () => {
        console.log(this.props.form.getFieldsValue());
        this.setState({ visible: false });
    }
    // 
    showModal = () => {
        this.setState({ visible: true });
    }    
    handleCancel = (e) => {
        this.setState({ visible: false });
    }  

    render() {
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 16 },
        };
        const areaData = [{
            value: 'audi',
            label: '',
            children: [{
                value: 'a6',
                label: 'AudiA6',                
            },{
                value: 'a8',
                label: 'AudiA8',                
            }],
        }];
        const { getFieldProps } = this.props.form;

        return (
            <div>
                <Button type="primary" onClick={this.showModal} size="large"></Button>
                <Modal title="" visible={this.state.visible} onOk={this.handleSubmit} onCancel={this.handleCancel}>
                    <Form horizontal>
                        <FormItem {...formItemLayout} label="">
                            <Input type="text" {...getFieldProps('planName')}/>
                        </FormItem>
                        <FormItem {...formItemLayout} label="">
                            <RadioGroup defaultValue="car" size="large" {...getFieldProps('goodsType')}>
                                <Radio key="a" value="car"></Radio>
                                <Radio key="b" value="ornament"></Radio>
                            </RadioGroup>
                        </FormItem>
                        <FormItem {...formItemLayout} label="">
                            <Cascader options={areaData}  {...getFieldProps('carType')}/>
                        </FormItem>
                        <FormItem {...formItemLayout} label="">
                            <InputNumber min={50} max={4000000}  {...getFieldProps('limit')} />/
                        </FormItem>
                        <FormItem {...formItemLayout} 
                            label={
                                <span>{' '} 
                                    <Tooltip title="">
                                        <Icon type="question-circle-o" />
                                    </Tooltip> 
                                </span>
                            }>
                            <Switch checkedChildren="" unCheckedChildren="" {...getFieldProps('line')}/>
                        </FormItem>                        
                    </Form>
                </Modal>
            </div>
        )
    }
}

let Btnform = Form.create()(AformInModal);
export default Btnform;


