import React from 'react';
import {Input,Row,Col,Card} from 'antd';

/*  */
export default class Salary extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            item:'',
            time:'',
            rent:'',
            buy:'',
            balance:'',          
            des:'',          
        }       
    } 

    handleChange = (e) => {
        let input = e.target.value;
        this.setState({ item: input });              
    }
    handleTime = (e) => {
        let input = e.target.value;
        this.setState({ time: input });
        let rent = input*12*this.state.item/10000;
        this.setState({ rent: rent });        
    }
    handleBuy = (e) => {
        let input = e.target.value;
        this.setState({ buy: input });
        let balance = input-this.state.rent;
        this.setState({ balance: balance });
        if(balance > 0) {
            this.setState({ des: '' });
        } else if(balance === 0) {
            this.setState({ des: '' });
        } else {
            this.setState({ des: '' });
        }  
    }
     
    render() {          
        return (
            <div style={{marginTop:80}}>
                <Row type="flex" justify="center" className="rowItem">
                    <Col span={10}>
                        <Input type="number" addonBefore="" addonAfter="/" onChange={this.handleChange}/>              
                    </Col>
                </Row>
                <Row type="flex" justify="center" className="rowItem">
                    <Col span={10}>
                        <Input type="number" addonBefore="" addonAfter=""  onChange={this.handleTime}/>
                    </Col>
                </Row>
                <Row type="flex" justify="center" className="rowItem">
                    <Col span={10}>
                        <Input addonBefore="" addonAfter="" value={this.state.rent} disabled id="red" />
                    </Col>
                </Row>
                <Row type="flex" justify="center" className="rowItem">
                    <Col span={10}>
                        <Input type="number" addonBefore="" addonAfter="" onChange={this.handleBuy} />
                    </Col>
                </Row>
                <Row type="flex" justify="center" className="rowItem">
                    <Col span={10}>
                        <Input addonBefore=" - " addonAfter="" value={this.state.balance} disabled id="blue" />
                    </Col>
                </Row>  
                <Row type="flex" justify="center" className="rowItem">
                    <Col span={10}>
                        <Card bodyStyle={{ padding:10,fontSize:20}}>
                            <p>{this.state.des}</p>
                        </Card>
                    </Col>
                </Row>          
            </div>
        );
    }
}




