import React from 'react';
import {Button,Checkbox,Select,Radio,Form,Row,Col,Icon,Input,InputNumber } from 'antd';

import './editForm.less';

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const RadioButton = Radio.Button;

class Editform extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    // 
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((errors, values) => {
            if (!!errors) {
              console.log('Errors in form!!!');
              return;
            }
            console.log('Submit!!!');
            console.log(values);
        });
    }
    // 
    handleReset = (e) => {
        e.preventDefault();
        this.props.form.resetFields();        
    }
    // ID
    checkNum = (rule, value, callback) => {
        if(value <1000000 || value >9999999) {
            callback(new Error('7'));
        } else {
            callback();
        }
    }

    render() {

        {/**/}
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 10 },
        };

        {/**/}
        const { getFieldProps, getFieldError, isFieldValidating } = this.props.form;

        {/**/}
        const curId = this.props.editData;

        {/**/}
        const nameProps = getFieldProps('planName', {
            rules: [
                { required: true, max:120, message: '120' }
            ],
            initialValue:' '+ curId
        });
        const idProps = getFieldProps('planId', {
            rules: [
                { validator:this.checkNum }
            ]
        });
        const radioProps = getFieldProps('status', {            
            initialValue:'qidong'
        });
        const baoProps = getFieldProps('baoguang', {
            rules: []
        });
        const baourlProps = getFieldProps('baoUrl', {
            rules: []
        });
        return (
            <div id="formWrapper">
                <Form horizontal>
                    <FormItem {...formItemLayout} label="" hasFeedback>
                        <Input type="text" {...nameProps} name="planName" />
                    </FormItem>
                    <FormItem {...formItemLayout} label="ID">
                        <InputNumber {...idProps} name="planId" />
                    </FormItem>
                    <FormItem {...formItemLayout} label="">
                        <RadioGroup {...radioProps} name="status">
                            <RadioButton value="qidong"></RadioButton>
                            <RadioButton value="zanting"></RadioButton>
                        </RadioGroup>
                    </FormItem>
                    <FormItem {...formItemLayout} label="">
                        <InputNumber {...baoProps} name="baoguang" />
                    </FormItem>
                    <FormItem {...formItemLayout} label="URL">
                        <Input {...baourlProps} type="url" name="baoUrl" />
                    </FormItem> 
                    <FormItem wrapperCol={{ span: 12, offset: 7 }} >
                        <Button type="primary" onClick={this.handleSubmit}></Button>&nbsp;&nbsp;&nbsp;                      
                        <Button type="ghost" onClick={this.handleReset}></Button>
                    </FormItem>                                     
                </Form>          
            </div>
        );
    }
}

// 
let EditForm = Form.create()(Editform);
export default EditForm;


