import React from 'react';
import {Table,Icon,Tooltip} from 'antd';
import { Link } from 'react-router';

// FetchIE
import 'whatwg-fetch';
import 'es6-promise/dist/es6-promise.min.js';
import 'fetch-ie8/fetch.js';

import './campaign.less';

// 
import Title from '../components/Title.jsx';
import Header from './components/header.jsx';

export default class Antdes extends React.Component {
    constructor(props) {
        super(props);
        this.state = {            
            tData:[],
            loading: true
        }        
    }    

    // 
    rowClick = (e) => {
        console.log(e.key);
    }

    refresh = (data) => {
        // 
        this.setState({tData:data.rowData});
    }

    // 
    fetchTableData = () => {
        fetch('../data/tableData.json')
            .then((res) => { console.log(res.status);return res.json(); })
            .then((data) => { this.setState({loading:false});this.setState({tData:data.rowData}); })
            .catch((e) => { console.log(e.message);});
    }    

    // (GET)
    componentDidMount() {
        this.fetchTableData();
    }         

    render() {  

        /**/
        const columns = [{
            title: '',
            dataIndex: 'name'  
        }, {
            title: 'ID',
            dataIndex: 'key'
        }, {
            title: '',
            dataIndex: 'status'
        }, {
            title: '',
            dataIndex: 'exp'
        },{
            title: 'URL',
            dataIndex: 'expURL',
            render: (text) => ( <a href={text} target="_blank">{text}</a> )
        }, {
            title: '',
            dataIndex: 'clickNum'
        },{
            title: '',
            dataIndex: 'clickRate',
            render: (text) => ( <span>{text}%</span> )
        }, {
            title: '',
            dataIndex: 'clickPrice'
        }, {
            title: '',
            dataIndex: 'limit'
        }, {
            title: '',
            dataIndex: 'handle',
            render: 
                (t,r,i) => (
                    <span>
                        <Tooltip title=""><Link to={"/edit/"+(i+1)}><Icon type="edit" /></Link></Tooltip>&nbsp;&nbsp;
                        <Tooltip title=""><Icon type="delete" style={{color:'#FD5B5B'}}/></Tooltip>
                    </span>
                )
        }];              
       
        return (
            <div id="wrap">                
                <Title name="Press F12, watch Console"/>
                <Header refreshTable={this.refresh} />
                <div id="table">
                    <Table 
                        rowSelection={{}} 
                        dataSource={this.state.tData} 
                        columns={columns} 
                        size="middle" 
                        pagination={{size:'large'}} 
                        onRowClick={this.rowClick}
                        loading={this.state.loading}
                    />                   
                </div>
            </div>
        )
  }
}



